<template>
    <div class="pieChart">      
        <canvas :id="data.id" class="myChart"   width="220" height="220"></canvas>
    </div>
</template>

<script>
export default {
  props:['data'],
  data () {
    return {
        newData:{
            data:[],
            total:''
        }
    }
  },
  methods:{
    drawLine(data){
            let _this = this;
            var canvas = document.getElementById(this.data.id),  //canvas
            context = canvas.getContext('2d'),  //2d
            centerX = canvas.width/2,   //Canvasx
            centerY = canvas.height/2,  //Canvasy
            rad = Math.PI*2/100, //360100rad
            speed = 0.1; // 
            

        //5
        function blueCircle(data){
            if(data.length == 0){
                return;
            }
            let totaln = data[0]+data[1]+data[2]+data[3]
            let n = data[0],n1=data[1],n2 =data[2],n3 = data[3];
            context.save();
            context.translate(centerX,centerY);
            context.strokeStyle = "#008aff"; // #ffa414  #008aff #fa6074 #4ae283
            context.lineWidth = 3; //
            context.beginPath(); //
            context.arc(0, 0, 100 , -Math.PI*4.5/3, -Math.PI*4.5/3 +n*rad, false); //context.arc(xy/)
            //context.arc(161,25,2,Math.PI*2,0,true);
            context.stroke(); //
            context.closePath(); //

            context.translate(Math.round(Math.cos((n*3.6+90)*Math.PI/180 )* 100),Math.round(Math.sin((n*3.6+90)*Math.PI/180 )* 100));
            context.fillStyle="#008aff";
            context.beginPath();
            context.arc(0,0,3, -Math.PI*4.5/3, -Math.PI*4.5/3 + Math.PI*2,false);
            context.fill();
            context.closePath();
            if(n1!==''){
                context.strokeStyle = "#4ae283";
                context.translate(-Math.round(Math.cos((n*3.6+90)*Math.PI/180 )* 100),-Math.round(Math.sin((n*3.6+90)*Math.PI/180 )* 100));
                context.beginPath(); //
                context.arc(0, 0, 90 , -Math.PI*4.5/3, -Math.PI*4.5/3 +n1*rad, false); //context.arc(xy/)
                context.stroke(); //
                context.closePath(); //
                // context.restore();

                context.translate(Math.round(Math.cos((n1*3.6+90)*Math.PI/180 )* 90),Math.round(Math.sin((n1*3.6+90)*Math.PI/180 )* 90));
                context.fillStyle="#4ae283";
                context.beginPath();
                context.arc(0,0,3, -Math.PI*4.5/3, -Math.PI*4.5/3 + Math.PI*2,false);
                context.fill();
                context.closePath();
            }
            if(n2!==''){
                context.strokeStyle = "#ffa414";
                context.translate(-Math.round(Math.cos((n1*3.6+90)*Math.PI/180 )* 90),-Math.round(Math.sin((n1*3.6+90)*Math.PI/180 )* 90));
                context.beginPath(); //
                context.arc(0, 0, 80 , -Math.PI*4.5/3, -Math.PI*4.5/3 +n2*rad, false); //context.arc(xy/)
                context.stroke(); //
                context.closePath(); //
                // context.restore();

                context.translate(Math.round(Math.cos((n2*3.6+90)*Math.PI/180 )* 80),Math.round(Math.sin((n2*3.6+90)*Math.PI/180 )* 80));
                context.fillStyle="#ffa414";
                context.beginPath();
                context.arc(0,0,3, -Math.PI*4.5/3, -Math.PI*4.5/3 + Math.PI*2,false);
                context.fill();
                context.closePath();
            }
            if(n1==='' && n2===''){
                context.translate(-Math.round(Math.cos((n*3.6+90)*Math.PI/180 )* 100),-Math.round(Math.sin((n*3.6+90)*Math.PI/180 )* 100));
                context.strokeStyle = "#fa6074";
                context.beginPath(); //
                context.arc(0, 0, 70 , -Math.PI*4.5/3, -Math.PI*4.5/3 +n3*rad, false); //
            }else{
                context.translate(-Math.round(Math.cos((n2*3.6+90)*Math.PI/180 )* 80),-Math.round(Math.sin((n2*3.6+90)*Math.PI/180 )* 80));
                context.strokeStyle = "#fa6074";
                context.beginPath(); //
                context.arc(0, 0, 70 , -Math.PI*4.5/3, -Math.PI*4.5/3 +n3*rad, false); //
            }
            //context.arc(xy/)
            context.stroke(); //
            context.closePath(); //

            context.translate(Math.round(Math.cos((n3*3.6+90)*Math.PI/180 )* 70),Math.round(Math.sin((n3*3.6+90)*Math.PI/180 )* 70));
            context.fillStyle="#fa6074";
            context.beginPath();
            context.arc(0,0,3, -Math.PI*4.5/3, -Math.PI*4.5/3 + Math.PI*2,false);
            context.fill();
            context.closePath();

            context.translate(-Math.round(Math.cos((n3*3.6+90)*Math.PI/180 )* 70),-Math.round(Math.sin((n3*3.6+90)*Math.PI/180 )* 70));            
            context.restore();
        }
        //
        function whiteCircle(){
            context.save();
            context.beginPath();
            context.lineWidth = 5; //
            context.strokeStyle = "rgba(0,0,0,0.2)";
            context.arc(centerX, centerY, 100 , 0, Math.PI*2, false);
            context.closePath(); //
            context.stroke(); //

            context.beginPath();
            context.arc(centerX, centerY, 90 , 0, Math.PI*2, false);
            context.closePath(); //
            context.stroke(); //

            context.beginPath();
            context.arc(centerX, centerY, 80 , 0, Math.PI*2, false);
            context.closePath(); //
            context.stroke(); //

            context.beginPath();
            context.arc(centerX, centerY, 70 , 0, Math.PI*2, false);
            context.stroke();
            context.closePath();

            context.beginPath();
            context.arc(centerX, centerY, 55 , 0, Math.PI*2, false);
            context.stroke();
            context.closePath();
            context.restore();
        }  
        //
        function text(n){
            context.save(); //saverestorecanvas
            context.strokeStyle = "#4A90E2"; //
            context.shadowOffsetX=4;
            context.shadowOffsetY=20;
            context.shadowColor='rgba(74,144,226,0.22)';
            context.shadowBlur = '20';

            context.font = "normal 30px Arial"; //
            //
            context.fillStyle = '#fff'
            context.fillText(n.toFixed(2)+"%", centerX-45, centerY+5);
            //context.strokeStyle = "#fff"; //
            context.font = "12px Arial"; //
            context.fillText("", centerX-25, centerY+35);
            context.stroke(); //
            context.restore();
        } 
        //
        (function drawFrame(){
            //window.requestAnimationFrame(drawFrame);
            context.clearRect(0, 0, canvas.width, canvas.height);
            whiteCircle();
            let num = data.total*100
            text(num);
            blueCircle(data.data);
        }());
  
    }
  },
  watch:{
    data:{
        handler(newValue, oldValue) {
   this.drawLine(newValue)
    },
    deep: true
    }
  },
  mounted() {
       this.drawLine(this.data);
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped="" type="text/less"> 
@import '../../../../assets/css/comon.less';
.pieChart{
    .vh(220);
    width:100%;
    position:relative; 
    .myChart{
      height:100%;
        width:100%;  
    } 
}

</style>



