<template>
  <div>
    <div ref="HpadTop" class="tabsDomBox0 h-paddingTop">
      <div class="navCrumbs"><p @click="toHome"></p> >  > <span></span></div>
    </div>
    <div
      class="fireHistory"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-spinner="el-icon-loading"
      element-loading-text=""
    >
      <div class="selectBox">
        <div class="dateBox">
          <el-date-picker
            v-model="dateVal"
            type="date"
            placeholder="">
          </el-date-picker>
        </div>
        <div class="dateBox" style="margin-left: 0.12rem">
          <el-date-picker
            v-model="dateVal2"
            type="date"
            placeholder="">
          </el-date-picker>
        </div>
        <div class="oneLevelBox">
          <el-select v-model="value" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="oneLevelBox">
          <el-select v-model="value2" placeholder="">
            <el-option
              v-for="item in options2"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="oneLevelBox">
          <el-select v-model="value3" placeholder="">
            <el-option
              v-for="item in options3"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="searchBox">
          <el-input placeholder="" v-model="posInput" class="input-with-select">
            <el-button @click="search" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </div>
        <div class="exportBox">
          <button
            @click="exportExcel()"
            type="button" class="self-button">
            <span class="icon-export"></span>
            <span></span>
          </button>
        </div>
      </div>

      <div class="self-table">
        <Table @changePage="changePage" :table="table"/>
      </div>
    </div>
  </div>
</template>
<script>
  import utils from "../../assets/js/utils.js";
  import Table from '../../components/common/table2';

  import showMark from './showMark'
  export default {
    components:{
      Table

    },
    name:'fireHistory',
    data () {
      return {
        loading:false,
        dateVal:'',
        dateVal2:'',
        //
        options: [{
          value: '',
          label: ''
        },{
          value: '17001',
          label: ''
        }, {
          value: '17002',
          label: ''
        }, {
          value: '17003',
          label: ''
        }],
        value: '',

        //
        options2: [{
          value: '',
          label: ''
        },
         /* {
          value: '17001',
          label: '001'
        }, {
          value: '17002',
          label: '002'
        }, {
          value: '17003',
          label: '003'
        }*/
        ],
        value2: '',

        //
        options3: [{
          value: '',
          label: ''
        },
          /*{
          value: '17001',
          label: ''
        }, {
          value: '17002',
          label: ''
        }*/
        ],
        value3: '',


        posInput:'',

        pagenum:1,//


        //
        table:{
          showPagination:true,
          hei:490, //table  
          len:0, //
          pageSize:20, // 20
          page:1,  //
          data:[
            /*{num:'01',time:'2018/06/02  16:55',position:'A1771',type:'',device:'001',state:0},
            {num:'02',time:'2018/06/02  16:55',position:'A1771',type:'',device:'001',state:1},*/
          ],
          th:[
            {prop:'num',label:'',wid:93},
            {prop:'time',label:'',wid:210},
            {prop:'position',label:'',wid:300},
            {prop:'type',label:'',wid:143},
            {prop:'device',label:''},//wid:184

            {prop:'state',
              label:'',
              operate: true,
              wid:90,
              render: (h, param)=> {
                const btnss = {
                  //data:param.row.type
                };
                //console.log(h,param)
                return h(showMark,{
                  props: {state:param.row.state},
                  on:{}
                });
              }
            },

          ]
        },

      }
    },
    methods:{
      //
      getHistory(num=1,date='',state='',type='',cont=''){
        this.loading = true;
        let that = this;
        let config = {
          project:this.$store.state.projectId,
          sys_menu_id:this.$store.state.sysList[16].sys_menu_id,
          pagesize:20,
          pagenumber:num,
          date:date,
          //state:state,
          cont:cont,
          type:type,
        }
        let headers = {
          //'Content-Type': 'multipart/form-data'
        }
        this.$http.post('firealarm/firealarm_record',config,headers).then(res=>{
          let data = res.data;
          console.log('',config,res);
          if (data.code==0){
              //
              this.table.len = data.paging.count;
              //
              let arr = [];
              let data2 = data.data;
              data2.map((item,i)=>{
                let num = (i+1)+(num-1)*20;
                let tempObj = {};
                tempObj.num = (num<10?'0'+num:num);
                tempObj.time = item.time;
                tempObj.position = item.floor_title;
                tempObj.type = item.class;
                tempObj.device = item.devicename;
                tempObj.state = (item.schedule==2?1:0);
                arr.push(tempObj)
              })

              //
              this.table.data = arr;
              this.loading = false;

          } else {
            this.loading = false;
            this.$message(data.message);
          }
        }).catch(err=>{
          this.loading = false;
          this.$message(err);
        })
      },

      //
      changePage(val){
        this.pagenum = val;
        this.getHistory(val,this.dateVal==''?'':utils.time(this.dateVal/1000),'',this.value,this.posInput)
      },

      //
      search(){
        this.getHistory(1,this.dateVal==''?'':utils.time(this.dateVal/1000),'',this.value,this.posInput)
      },

      exportExcel(){
        window.location.href = "https://tesing.china-tillage.com/firealarm/firealarm_record_excel?sys_menu_id="+this.$store.state.sysList[16].sys_menu_id+"&project_id="+this.$store.state.projectId+"&pagesize=500&pagenumber=1"
      },

      toHome(){
        this.$router.replace({ path: '/home', params: { isLogin: true} });
      }





    },
    created() {
      this.getHistory();

    },
    mounted() {
      this.$refs.HpadTop.style.paddingTop = Number(this.$parent.$children[0].$el.children[0].offsetHeight)+30+'px';
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" type="text/less">
  /**/
  .el-popper[x-placement^=bottom] .popper__arrow{
    border-bottom-color:#04152c!important;
    &::after{
      border-bottom-color:#04152c!important;
    }
  }
  @import '../../assets/css/common.css';
  @import '../../assets/css/comon';
  .el-popper[x-placement^=bottom] .popper__arrow{
    border-bottom-color: #4A90E2 !important;
  }
  .fireHistory{
    margin: 0 auto;
    margin-top: 0.1rem;
    width: 13.06rem;
    .vh(610);
    /*background-color: #000000;*/
    box-shadow: 0px 4px 10px 0px
    rgba(73, 143, 226, 0.22),
    inset 1px 1px 2px 0px
    rgba(248, 253, 255, 0.15),
    inset 0px -1px 1px 0px
    #498fe2;
    border-radius: 4px;

    .selectBox{
      /*#04152c*/
      width: 100%;
      padding: 0.16rem;
      display: flex;
      align-items: center;

      .el-input__inner{
         font-size: 0.12rem!important;
        line-height: 0.32rem;
         color: #fff!important;
         background-color: rgba(0,0,0,0)!important;
         border-radius: 0.02rem!important;
        border-top-right-radius: 0!important;
        border-bottom-right-radius: 0!important;
         border: solid 1px #1989fa!important;
         height: 0.32rem!important;
      }
      .dateBox{
        display: inline-block;
        width: 1.59rem;
      }
      .oneLevelBox{
        margin-left: 0.12rem;
        display: inline-block;
        width: 0.88rem;
      }
      .searchBox{
        margin-left: 2.67rem;
        display: inline-block;
        width: 2.71rem;
        .el-input-group__append{
          background-color: #3a84ed!important;
          vertical-align: middle;
          display: table-cell;
          position: relative;
          border: 0!important;
          border-radius: 2px!important;
          border-top-left-radius: 0!important;
          border-bottom-left-radius: 0!important;
          padding: 0 0.2rem!important;
          width: 1px;
          white-space: nowrap;

          font-family: PingFangSC-Medium;
          font-size: 0.14rem;
          font-weight: normal;
          font-stretch: normal;
          line-height: 1;
          letter-spacing: 0px;
          color: #ffffff!important;
        }
      }
      .exportBox{
        margin-left: 0.12rem;
        display: inline-block;
        width: 0.88rem;
        height: 100%;
        .self-button{
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          width: 0.88rem;
          height: 0.32rem;
          background-color: #3a84ed;
          color: #fff;
          border: 0;
          outline: none;
          border-radius: 2px;
          padding: 0;
          font-size: 0.14rem;
          font-weight: normal;
          font-stretch: normal;
          line-height: 1;
          letter-spacing: 0px;
          .icon-export{
            display: inline-block;
            width: 0.14rem;
            height: 0.14rem;
            background: url("../../assets/img/fireAlarm/icon_export.png") no-repeat center;
            background-size: 100% 100%;
            margin-right: 0.08rem;
          }
        }
      }
    }

    .self-table{
      width: 100%;
      padding: 0 0.16rem;
      .el-table__header-wrapper{
        background-color: rgba(0,0,0,0.25)!important;
      }
    }

  }

</style>



