<template>
  <div class="userBox" >
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <div class="contentBox boxs">
      <!---->
      <div class="itemsBox">
        <!---->
        <div class="queryBox">
          <el-select placeholder="" v-model="itemValue[0]" class="querySelectItem" clearable >
            <el-option
              v-for="item in options[0]"
              :key="item.id"
              :label="item.title"
              :value="item.id">
            </el-option>
          </el-select>
          <el-select placeholder="" v-model="itemValue[1]" class="querySelectItem" clearable >
            <el-option
              v-for="item in options[1]"
              :key="item.id"
              :label="item.title"
              :value="item.id">
            </el-option>
          </el-select>
          <el-select placeholder="" v-model="itemValue[2]" class="querySelectItem" clearable >
            <el-option
              v-for="item in options[2]"
              :key="item.id"
              :label="item.title"
              :value="item.id">
            </el-option>
          </el-select>
          <el-input
            placeholder=""
            v-model="itemValue[3]"
            class="userNameInput"
            clearable>
          </el-input>
          <div>
            <el-button class="queryDataBtn queryBoxBtn" @click="queryData"><i></i><span></span></el-button>
          </div>
        </div>

        <!---->
        <div>
          <el-button class="addNewUserBtn queryBoxBtn" @click="addAccount"><i></i><span></span></el-button>
        </div>
      </div>

      <!---->
      <div class="userTableContainer">
        <el-table
          v-loading="userLoading"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.3)"
          :data="curPageData"
          style="width: 100%"
          class="tableAlignCenter tableHeadBlue">
          <el-table-column
            prop="code"
            label=""
            min-width="10%">
          </el-table-column>
          <el-table-column
            prop="username"
            label="()"
            min-width="10%">
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            min-width="16%">
          </el-table-column>
          <el-table-column
            prop="dept_title"
            label=""
            min-width="12%">
          </el-table-column>
          <el-table-column
            prop="position_title"
            label=""
            min-width="12%">
          </el-table-column>
          <el-table-column
            prop="role_title"
            label=""
            min-width="12%">
          </el-table-column>
          <el-table-column
            prop="create_date"
            label=""
            min-width="19%">
          </el-table-column>
          <el-table-column
            prop="operation"
            label=""
            min-width="9%">
            <template slot-scope="scope">
              <a href="javascrtip:;" @click="editAccount(scope.row)" class="tableBtn"></a>
              <a href="javascrtip:;" @click="deleteAccount(scope.row)" class="tableBtn"></a>
            </template>
          </el-table-column>
        </el-table>

        <!---->
        <div class="paginationBox">
          <div class="totalPageNumBox">{{totalPageNum}}</div>

          <div class="el-input el-pagination__editor is-in-pagination curPageBox">
            <input type="number" autocomplete="off" class="el-input__inner" v-model="toPageNum">
            <span @click="toInputPage">GO</span>
          </div>

          <el-pagination
            @current-change="currentPageChange"
            :current-page="currentPage"
            :page-size="pageSize"
            :page-count="totalPageNum"
            layout="prev, pager, next"
            >
          </el-pagination>

        </div>

      </div>

      <!---->
      <el-dialog :title="formTitle" :visible.sync="accountInfoDialog" class="dialogBox" :close-on-click-modal="false" @close="clearForm">
        <el-form :model="form">
          <el-form-item :label="form.name.label" >
            <el-input v-model="form.name.key" autocomplete="off" placeholder="" id="userPhoneInput"  @blur="userNameBlur"></el-input>
          </el-form-item>
          <el-form-item :label="form.password.label" v-if="isAdd">
            <el-input v-model="form.password.key" type="password" autocomplete="off" placeholder="" :disabled="true" ></el-input>
          </el-form-item>
          <el-form-item :label="form.password.label" v-else>
            <el-button @click="resetPw" class="queryBoxBtn resetPwBtn" :class="{ loading: isReset }"><i></i> </el-button>
          </el-form-item>
          <el-form-item :label="form.department.label" >
            <el-select v-model="form.department.key" placeholder="" >
              <el-option :label="item.title" :value="item.id" v-for="item in options[0]" :key="item.id" ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item :label="form.position.label" >
            <el-select v-model="form.position.key" placeholder="">
              <el-option :label="item.title" :value="item.id" v-for="item in options[1]" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item :label="form.role.label" >
            <el-select v-model="form.role.key" placeholder="">
              <el-option :label="item.title" :value="item.id" v-for="item in options[2]" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="saveNewAccount(isAdd)" class="saveBtn"></el-button>
          <el-button @click="accountInfoDialog = false" class="cancleBtn"></el-button>
        </div>
        <bubbleTip :tipText="bubbleTip"/>
      </el-dialog>

      <!---->
      <el-dialog :visible.sync="deleteInfoDialog" class="dialogBox deleteInfoDialog" :close-on-click-modal="false">
        <div class="deleteTextBox"></div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="confirmDelete()" class="saveBtn"></el-button>
          <el-button @click="deleteInfoDialog = false" class="cancleBtn"></el-button>
        </div>
        <bubbleTip :tipText="bubbleTip"/>
      </el-dialog>

    </div>

  </div>
</template>

<script>
    import bubbleTip from '@/components/common/bubbleTip';
    import axios from 'axios';

    export default {
      components:{
        "bubbleTip":bubbleTip
      },
      name: "user-setting",
      data(){
        return {
          userLoading:true,
          sysInfo:{}, //
          options: [], //
          itemValue:[], //
          tableData: [], //
          currentPage: 1, //
          pageSize: 20, //
          totalPageNum: 1,
          totalDataNumber: 20,
          curPageData:[], //
          groupPageData:[],
          accountInfoDialog:false, //
          deleteInfoDialog:false, //
          form: {
            name: {label:"", key:""},
            password:{label:"", key:""},
            department: {label:"", key:""},
            position: {label:"", key:""},
            role: {label:"", key:""}
          }, //
          formTitle:"", //
          isAdd:true, //
          bubbleTip:'',
          isReset:false, //
          phoneRegexp:/^0?(13|14|15|17|18|19)[0-9]{9}$/,
          curEditUserId:'', //id
          curEditUserPw:'', //
          toPageNum: 1
        }
      },
      methods:{
        userNameBlur(){
          let that = this;
          let name = that.form.name.key;
          if(that.phoneRegexp.test(name)){
            that.form.password.key = (name).substr(5);
          }else {
            that.bubbleTipShow("");
          }
        },
        queryData(){
          /**/
          this.requestTableData(1);
        },
        currentPageChange(val) {
          /**/
          this.currentPage = this.toPageNum = val;
          this.requestTableData(val);
        },
        getCurPageData(data){
          /**/
          let that = this;
          that.curPageData = data.data;
          that.totalDataNumber = data.paging.count;
          that.totalPageNum =  Math.ceil(Number(that.totalDataNumber) / that.pageSize);
        },
        toInputPage(){
          /**/
          var num = Number(this.toPageNum);
          this.requestTableData(num);
          this.currentPage = num;
        },
        addAccount(){
          /**/
          this.accountInfoDialog = true;
          this.formTitle = '';
          this.isAdd = true;
        },
        saveNewAccount(type){
          /**/
          let that = this;
          for(var i in that.form){
            if(!type && i=='password'){
              continue;
            }
            let temp = that.form[i].key;

            if(!temp){
              that.bubbleTipShow("");
              return;
            }else {
              if(i == 'name'){
                if(!that.phoneRegexp.test(temp)){
                  that.bubbleTipShow("");
                  return;
                };
              }
            }
          }
          let userId;
          type?userId=0 : userId=that.curEditUserId;

          let config = {
            user_id : userId,
            phone : that.form.name.key,
            username : that.form.name.key,
            pwd : that.form.password.key,
            dept_id : that.form.department.key,
            posit_id : that.form.position.key,
            role_id : that.form.role.key,
            address : ""
          }

          console.log(config);

          that.$http.post('users_manage/users_addmodify',config).then(res=>{
            console.log(res);
            if(res.data.code =='0'){
              let message = type?"":"";
              that.bubbleTipShow(message);
              setTimeout(function () {
                that.accountInfoDialog = false;
                that.clearForm();
                that.requestTableData(1);
              },2000)
            }else {
              that.bubbleTipShow(res.data.message);
            }
          }).catch(err=>{
            console.log(err);
          })

        },
        editAccount(val){
          /**/
          console.log(val);
          this.accountInfoDialog = true;
          this.formTitle = '';
          this.isAdd = false;
          this.form.name.key = val.username;
          this.form.department.key = val.dept_id;
          this.form.position.key = val.position_id;
          this.form.role.key = val.role_id;
          this.curEditUserId = val.user_id;
        },
        deleteAccount(val){
          var that = this;
          that.deleteInfoDialog = true;
          that.curEditUserId = val.user_id;
        },
        confirmDelete(){
          let that = this;
          let config = {
            user_id : that.curEditUserId
          }

          that.$http.post('users_manage/users_delete',config).then(res=>{
            if(res.data.code =='0'){
              that.isReset = false;
              that.bubbleTipShow('');
              setTimeout(function () {
                that.deleteInfoDialog = false;
                that.requestTableData(1);
              },2000)
            }else {
              that.bubbleTipShow(res.data.message);
            }
          }).catch(err=>{
            console.log(err);
          })
        },
        resetPw(){
          /**/
          var that = this;
          that.isReset = true;
          var autoPw = (that.form.name.key).substr(5);

          let config = {
            user_id : that.curEditUserId,
            pwd : autoPw
          }

          that.$http.post('users_manage/users_reset_password',config).then(res=>{
            if(res.data.code =='0'){
              that.isReset = false;
              that.bubbleTipShow('');
            }else {
              that.bubbleTipShow(res.data.message);
            }
          }).catch(err=>{
            console.log(err);
          })
        },
        clearForm(){
          for(var i in this.form){
            this.form[i].key = '';
          }
        },
        bubbleTipShow(tip){
          this.bubbleTip = tip;
          this.$store.state.bubbleShow = true;
          var that = this;
          setTimeout(function () {
            that.$store.state.bubbleShow = false;
          },3000)
        },
        requestOptions(){
          /**/
          let that  = this;

          axios.all([
            that.$http.post('/users_manage/users_department',{
              project_id: that.sysInfo.projectId
            }),
            that.$http.post('/users_manage/users_dept_position',{
              project_id: that.sysInfo.projectId
            }),
            that.$http.post('/users_manage/users_roleinfo_list',{
              user_role_id:that.sysInfo.roleId
            })
          ]).then(axios.spread(function (departResp, positionResp,roleResp) {

            // 
            var optionArray = [];
            optionArray[0] = departResp.data.data;
            optionArray[1] = positionResp.data.data;
            optionArray[2] = roleResp.data.data;

            for(var val of optionArray){
              var temp = {id: "" ,title: ""};
              val.unshift(temp);
            }
            that.$store.state.permission.options = that.options = optionArray;
            that.$forceUpdate();
          }));
        },
        requestTableData(curPageNum){
          /**/
          let that = this;
          that.userLoading = true;
          let config = {
            project_id : that.sysInfo.projectId,
            pagenumber : curPageNum,
            pagesize : that.pageSize,
            role_type : "",
            dept_id : that.itemValue[0],
            posit_id : that.itemValue[1],
            role_id : that.itemValue[2],
            username : that.itemValue[3],
            user_role_id : Number(that.sysInfo.roleId)
          }

          that.$http.post('users_manage/usersinfo',config).then(res=>{
            console.log(res);
            that.getCurPageData(res.data);
            that.userLoading = false;
          }).catch(err=>{
            console.log(err);
            that.userLoading = false;
          })
        }
      },
      created(){
        var that = this;
        that.sysInfo.projectId = that.$store.state.projectId;
        that.sysInfo.roleId = that.$store.state.userInfoTotal.role_info.role_id;
        that.sysInfo.roleType = that.$store.state.userInfoTotal.usergrouprolesyslist[0].role_type;

        var option = that.$store.state.permission.options;
        if(option.length>0){
          that.options = option;
        }else {
          that.requestOptions();
        }

        that.requestTableData(1);
      },
      mounted(){

      }
    }
</script>

<style scoped>


</style>



